/**
 * = Buttons
 */

.btn {
    position: relative;
    transition: $transition-base;
    letter-spacing: $btn-letter-spacing;
    font-size: $btn-font-size;
    border-color: $light;

    &:focus,
    &.focus {
        outline: 0;
        @include box-shadow(none);
    }

    &:not(:disabled):not(.disabled) {
    cursor: if($enable-pointer-cursor-for-buttons, pointer, null);

    &:active,
    &.active {
      @include box-shadow(none);

      &:focus {
        @include box-shadow(none);
      }
    }

     &:not(:disabled):not(.disabled):active,
     &:not(:disabled):not(.disabled).active {
        @include box-shadow(none);
     }
  }


	&.btn-pill {
		border-radius: $border-radius-pill;
    }

    &.btn-circle {
        border-radius: $circle-radius;
    }
    

    .btn-group &, .input-group & {
        margin-right: 0;
        transform: translateY(0);
    }

    .toggle-arrow {
        transition: $transition-base;
    }

    &[aria-expanded="true"] {
        .toggle-arrow {
            @include transform(rotate(180deg));
        }
    }
	
}




// Fixes
[class*="btn-outline-"] {
    border-width: 2px;
}

.btn-outline-secondary {
    color: $secondary;
}

.btn-inner-icon {
    i:not(.fa) {
        position: relative;
    }
}

.btn-link {
    font-weight: $btn-font-weight;
    box-shadow: none;
    padding: 0;

	&:hover,
	&:focus,
	&:active {
		background-color: transparent;
		box-shadow: none;
        transform: none;
	}
}

// Icons 
.btn-icon {
    display: inline-block;

    .btn-inner-icon:first-of-type {
        margin-right: .3rem;
    }
    .btn-inner-icon:last-of-type {
        margin-left: .3rem;
    }
}

.btn-icon-only {
    position: relative;
    width: 2.575rem;
    height: 2.575rem;
    padding: 0;

    span, i {
        position: absolute;
        top: 50%;
        left: 50%;
        @include translate(-50%, -50%);
    }

    &a {
        line-height: 2.5;
    }

    &.btn-xs {
        width: 1.7rem;
        height: 1.7rem;
    }
    &.btn-sm {
        width: 2rem;
        height: 2rem;
    }
}

.spinner-border,
.spinner-brow {
    vertical-align: middle;
}

.btn-loading-overlay {
    .spinner {
        position: absolute;
        top: 50%;
        left: 50%;
        @include transform(translate(-50%, -50%));
        opacity: 0;
    }
    .btn-inner-text,
    .spinner {
        transition: $transition-base;
    }
    .btn-inner-text {
        opacity: 1;
    }
    &.btn-loading {
        .spinner {
            opacity: 1;
        }
        .btn-inner-text {
            opacity: 0;
        }
    }
}

// Set color of theme buttons
@each $color, $value in $theme-colors {
    .btn-#{$color}{
        @include button-variant($value, $value);
    }
}


// Brand (social) buttons 
@each $color, $value in $brand-colors {
    .btn-#{$color} {
        background-color: $primary;
        color:$value;
        border-color:$light;
        &:hover{
            color:$value;
            background-color: transparent;
            border-color:$light;
        }
		&.btn-link {
			color: $value;
			background: transparent;
            border:0;
            border-style: none;
			&:hover,
			&:focus,
			&.active {
                background-color: none !important;
                border: 0;
				color: $value;
			}
		}
    }
}
