/**
 * = Navigation 
 */
.navbar {
    background: rgba(255,255,255,.9);
}

// Navbar brand
.navbar-brand {
   > img {
        width: 40px;
        border-radius: 50%;
    }
}


 @include media-breakpoint-down(md) {
    .navbar-nav {
        float: right;
        width: 120px;
        background-color: $gray-300;
        border-radius: .3rem;
    }
    .nav-item {
        padding: 0 1rem;
    }
  }