/**
 * = Cards
 */
.card {
    position: relative;
    background-color: $bg-default;
    .card-header{
        background: transparent;
    }
}

.card-footer{
    background-color:transparent;
}

.card-service {
    min-height: 344px;
    @include box-shadow($box-shadow-sm);
    &:hover {
        transform: translateY(-20px);
        @include transition($transition-base);
        @include transition(.2s);
        @include box-shadow($box-shadow-light);
    }
    .card-img-top {
        margin: 20px auto 0;
        max-width: 50%;
        border-radius: 50%;
    }
}

.card-team {
    margin: 5rem 2rem 0;
    // min-height: 344px;
    border: 1px solid $gray-200;
    @include box-shadow($box-shadow-sm);
    &:hover {
        transform: translateY(-20px);
        @include transition($transition-base);
        @include transition(.2s);
        @include box-shadow($box-shadow-light);
    }
    .card-title{
        margin-bottom: .4rem;
    }
    .card-img-top {
        margin: -5rem auto 0;
        max-width: 50%;
        border-radius: 50%;
        border: 1px solid $gray-200;
    }
    .card-foot {
        font-size: 24px;
    }
}

.card-contact {
    min-height: 324px;
    @include box-shadow($box-shadow-sm);
    &:hover {
        transform: translateY(-20px);
        @include transition($transition-base);
        @include transition(.2s);
        @include box-shadow($box-shadow-light);
    }
    .card-icon-top {
        display: inline-block;
        margin: 20px auto 0;
        border-radius: 50%;
        font-size: 3.6rem;
        width: 100px;
        height: 100px;
        line-height: 100px;
        text-align: center;
        overflow: hidden;
        &::before {
            vertical-align: middle;
        }
    }
}

.card-about {
    background-color:transparent;
    .card-img {
        display: block;
        margin: 0 auto;
        max-width: 90%;
        border-radius: 50%;
    }
}

.card-gallery{
    @include box-shadow($box-shadow-sm);
    &:hover {
        @include box-shadow($box-shadow-light);
    }
}

.card-blog{
    border: 1px solid $gray-300;
    @include box-shadow($box-shadow-sm);
    &:hover {
        @include box-shadow($box-shadow-light);
    }
    .card-title {
        margin-top: 0;
    }
}

.card-side {
    border: 1px solid $gray-300;
    margin-bottom: 1.6rem;
    .badge {
        margin-bottom: .5rem;
    }
}


 @include media-breakpoint-down(sm) {
    .card-about {
        .card-title {
            text-align: center;
        }
    }
  }