// blog
.blog-banner {
  margin-top: 75px;
  height: 20rem;
  background-image: url(../../../assets/img/blog/banner.jpg);
  background-position: center center;
  .container {
    height: 20rem;
  }
  .blog-title {
    padding: 1rem 2rem;
    width: 100%;
    border-radius: 2rem;
    background-image: linear-gradient(to right, rgba(255,255,255,.9), rgba(255,255,255,0));
  }
}

.blog-meta{
  padding-bottom: 1.2rem;
  .blog-author{
    img {
      width: 20px;
      vertical-align: -5px;
    }
  }
}

.blog-main {
  .card-columns{
    column-count: 2;
    column-gap: 2rem;
  }
  .card-blog {
    margin-bottom: 2rem;
  }
}

.blog-content {
  img {
    margin: 1rem 0;
    border-radius: .5rem;
    @include box-shadow($box-shadow-light);
  }
}

 @include media-breakpoint-down(md) {
    .blog-banner{
      height: 10rem;
      .container {
        height: 10rem;
      }
    }

    .blog-main {
      .card-columns{
        column-count: 1;
        column-gap: 1rem;
      }
    }

    .blog {
      .card-columns{
        column-count: 2;
      }
    }
  }

   @include media-breakpoint-down(sm) {


    .blog {
      .card-columns{
        column-count: 1;
      }
    }
  }